import React, { Component } from "react";

//   
//yarn add react-slick
//npm install slick-carousel

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

//
// function MainImg() {
class MainImg extends Component {
    render() {
        const settings = {
            dots: true, // 
            infinite: true, // loop 
            speed: 500, // 
            autoplay: true, //  
            autoplaySpeed: 3000, // 
            slidesToShow: 1, //   
            slidesToScroll: 1, //  
        };

        return (
            <div className="mainimg">
                <Slider {...settings}>
                    <div>
                        <img
                            src="https://shoppingcdn.namyangi.com/attach/site/2022/202212/8d1c3f9b-5a67-4fcc-b414-7f946c0beed2.png?b9d15b2"
                            alt=""
                        />
                    </div>
                    <div>
                        <img
                            src="https://shoppingcdn.namyangi.com/attach/site/2022/202210/bc86ba53-803a-4c49-9b29-b60b6fa2afe8.png?b9d15b2"
                            alt=""
                        />
                    </div>
                    <div>
                        <img
                            src="https://shoppingcdn.namyangi.com/attach/site/2020/202011/2eb7cd86-ff23-4e52-82ad-00916b8424e3.png?b9d15b2"
                            alt=""
                        />
                    </div>
                </Slider>
            </div>
        );
    }
}

export default MainImg;
